<template>
    <div class="collapse">
        <slot></slot>
    </div>
</template>

<script>
    import Vue from "vue"
	export default {
		name: 'collapse',
        props:{
			selected:{
				type:Array,
            },
			onlyone:{
				type:Boolean,
				default:false
            }
        },
        provide() {
			return {eventBus:this.eventBus}
		},
        data(){
			return {
				eventBus: new Vue()
            }
        },
        mounted() {
			console.log(this.onlyone);
            if (this.onlyone) {
				// 
				this.$children.forEach((vm, index) => {
					console.log(vm)
					vm.onlyone = this.onlyone;
				})
			}
            // 
            this.eventBus.$on('update:selected',(vm)=>{
            	//
                let arr = JSON.parse(JSON.stringify(this.selected));
            	if(vm.open){
                    arr.push(vm.name)
                }else {  //
            		arr.splice(arr.indexOf(vm.name),1);
                }
            	this.$emit('update:selected',arr);
            })

            //
			this.eventBus.$emit('update:checked',this.selected);
		}
	};
</script>

<style lang="scss" scoped>
    .collapse{
        border-top: 1px solid #ebeef5;
        border-bottom: 1px solid #ebeef5;
    }
</style>
